@use "base/variables" as v;
@use "base/mixins" as m;

.video {
    position: relative; // * mi mapa para el overlay
    // height: 70rem;
    min-height: 50rem;
    max-height: 70rem;
    overflow: hidden; // ! aquel elemento que exceda el tamaño de mi contenido (video) será cortado (ocultará)
    .overlay {
        position: absolute;
        // background: rgb(255, 0, 0);
        background: linear-gradient(
            90deg,
            rgba(255, 0, 0, 0.3) 0%,
            rgba(255, 0, 249, 0.3) 100%
        );
        // * establecer el width es necesario para que se le aplique el "margin: 0 auto;", recordado por Codely TV
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        z-index: 1; // para que se posicione adelante. debido a que el video se ponia encima (no lo tapaba pero lo dejaba seleccionarlo)
    }
    video {
        // width: auto;
        display: block;
        @include m.desktop {
            width: 100%
        };
        // * para el desborde (borrar la linea de abajo) // juan: "para que tome todo el espacio hacia abajo"
    }
    // @include m.tablet {
    //     height: 100rem;
    // };
}

.contenido-video {
    // background-color: red;
    h2 {
        color: v.$blanco;
        margin: 0;
        font-size: 7rem;
    }
    p {
        color: v.$blanco;
        margin: 0;
        background-color: v.$rosa;
        display: inline-block; // para que su padding también se incluya como bloque
        padding: 1rem 2rem;
        font-weight: 700;
        font-size: 2.4rem;
    }
}
