@use "base/variables" as v;
@use "base/mixins" as m;

.boletos {
    padding: 5rem 0;

    h3 {
        text-align: center;
        color: v.$blanco;
        margin-bottom: 2rem;
    }

    .contenedor-boletos {
        @include m.tablet {
            @include m.grid(2, 5rem);
        }
    }
}

.pase {
    color: v.$negro;
    .nombre-pase {
        color: v.$blanco;
        text-align: center;
        font-size: 2.8rem;
    }
    &-header,
    &-footer {
        padding: 0.5rem;
        p {
            text-align: center;
            color: v.$blanco;
            margin: 0; // los parrafos tienen margin
        }
    }

    &-header {
        background-color: v.$rosa;
        p {
            font-size: 1.6rem;
            text-transform: uppercase;
        }
    }
    &-body {
        background-color: v.$blanco;
        ul {
            @include m.resetear-ul;
            li,
            :nth-child(odd) {
                background-color: v.$blanco;
                padding: 1rem;
                border-bottom: 1px solid #e1e1e1;
            }
        }
    }
    &-footer {
        background-color: v.$morado;
        // padding: 0.5px 0;
        p {
            font-size: 2.4rem;
            font-weight: 700;
        }
    }
}
