@mixin phone {
    @media (min-width: v.$phone) {
        @content; // * para agregarle contenido al momento de usarlo
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin grid($columns, $gap) {
    display: grid;
    grid-template-columns: repeat($columns, 1fr);
    gap: $gap; // para aplicarle tanto a las columnas como a las filas en caso tengas varias de ambas (filas y columnas separadas)
}

@mixin resetear-ul {
    padding: 0;
    margin: 0;
    list-style: none;
}
