@use "base/variables" as v; // lo pongo con su carpeta raiz pq esta en otra carpeta
@use "base/mixins" as m; // importamos nuestro scss de mixins

.header {
    background-color: v.$verde;
    // no se recomienda tener muchas anidaciones, por ejemplo colocar el .contenido-header aquí, por el otro selector para el h1. si lo colocas entonces en el .css aparecerán 3 selectores (.header .contenido-header h1 {} ), y eso trae problemas ademas de que no es tan correcto
    @include m.tablet {
        &.fijo {
            position: fixed;
            width: 100%;
            box-shadow: 0.1rem 0.2rem 0.3rem #333;
            // necesario para devolvera su sitio mi barra fija (y que no le afecte el padding-top)
            top: 0;
            left: 0;
        }
    }
}

.contenido-header {
    // lo colocamos aqui y no dentro del .header
    // background-color: red;
    padding: 2rem;
    h1 {
        // menciono a mi h1 anidado a mi header (checa el .css compilado)
        color: v.$blanco;
        margin-bottom: 2rem;
        text-align: center;
        @include m.tablet {
            margin: 0;
            text-align: left; // al achicar al limite entre phone y tablet se muestra bien empleado esta propiedad
        }
    }

    @include m.tablet {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}

// * ejemplo de mixins con parametros
// .mixeo {
