.navegacion-principal {
    a {
        display: block; // se pone bloque pq (el "a" o sea el link) toma todo el ancho. en este caso es mejor esto, pq si le pones un flex column al .navegacion (el "a") ya no tomaria todo el ancho de la pantalla (y no seria clickeable)
        // background-color: red;
        text-align: center; // recuerda que esto también alinea a la versión de escritorio
        margin-bottom: 1rem;
        color: v.$blanco;
        font-size: 2.4rem;
        @include m.tablet {
            margin: 0;
        }
        &:hover {
            // el "&" se usa para eliminar el espacio (en nuestro .css) que sass crea al compilar esta anidación
            color: v.$amarillo;
        }
    }
    @include m.tablet {
        display: flex;
        column-gap: 2rem;
    }
}
