@use "base/variables" as v;
@use "base/mixins" as m;

.lineup {
    background-color: v.$rosa;
    padding: 5rem 0;
    h3,
    p {
        text-align: center;
        color: v.$blanco;
    }
    .dia {
        text-transform: uppercase;
        font-size: 2rem;
        margin: 5rem 0;
    }
}

.escenarios-contenedor {
    @include m.tablet {
        @include m.grid(2, 2rem);
    }
    p {
        color: unset;
    }
}
.escenario {
    // display: block;
    padding-top: 2rem;
    border-radius: 1rem;
    // margin-top: 4rem;
    .nombre-escenario {
        text-align: center;
    }
}

.escenario .nombre-escenario::after { // haciendolo asi ves que tampoco es que sea necesario complicarte a veces usando lo de la anidación si ya conoces el orden que quieres establecer (.escenario .nombre-escenario)
    // :after agrega un pseudoelemento después de la clase ".nombre.escenario"
    content: ""; // ! 1° uso obligatorio // pero ningun contenido agregado
    display: block; // ! 2° uso obligatorio
    // lo siguiente es como si creara una caja o div (heigh width) y dentro le pusiera un img (background)
    width: 20rem; // a. obligatorio establecer ancho DE MI CAJA
    height: 10rem; // b. obligatorio establecer alto DE MI CAJA
    background-size: 15rem; // * ancho de la imagen que uso como background
    background-repeat: no-repeat;
    background-position: center; // lo mismo que center center (o sea en x y)
    margin: 2rem auto 0 auto; // centra mi pseudoelemento creado
}

.rock .nombre-escenario::after { // colocando el background de rock para las clases .rock .nombre-escenario
    background-image: url(../img/icono_rock.png);
}
.edm .nombre-escenario::after { // colocando el background de rock para las clases .edm .nombre-escenario
    background-image: url(../img/icono_edm.png);
}

.calendario {
        @include m.resetear-ul;
    li {
        font-weight: 700;
        padding: 2.5rem;
        span {
            font-weight: 400;
        }
    }
}

.escenario:last-child {
    margin-top: 4rem;
    @include m.tablet {
        margin-top: unset;
    }
}

.bg-amarillo {
    background-color: v.$amarillo;
    li:nth-child(odd) { // odd = 2n+1
        background-color: darken(v.$amarillo, 20%); // * darken es propio de sass
    }
}

.bg-verde {
    background-color: v.$verde;
    li:nth-child(2n + 1) {
        background-color: darken(v.$verde, 10%);
    }
}
