@use "base/variables" as v;
@use "base/mixins" as m;

.galeria {
    padding: 5rem 0;
    h3 {
        text-align: center;
    }
    .galeria-imagenes {
        @include m.resetear-ul;
        @include m.grid(2, 1rem);
        @include m.phone {
            @include m.grid(3, 1rem);
        }
        @include m.tablet {
            @include m.grid(4, 1rem);
        }
        @include m.desktop {
            @include m.grid(6, 1rem);
        }
    }
}

.overlay {
    position: fixed;
    // creo que esto es para que tome todo el ancho y alto DE LA PANTALLA
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    // supuestamente para asegurarnos
    width: 100%;
    height: 100%; // ? curioso que no tengas que usar el 100vh -> creo que es por la definicion de position: fixed -> se posiciona relativo al viewport (ventana grafica)
    
    // también podrias usar flex para centrarlo
    display: grid;
    place-items: center;

    background-color: rgb(0 0 0 / 0.5);

    picture {
        display: block; // ! es obligatorio para que el max-width funcione // teoria block vs inline
        max-width: 80%;
    }
    // img {
    //     height: 50%;
    //     width: 50%;
    // }

    
}
.btn-cerrar {
    background-color: v.$blanco;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    display: grid;
    place-items: center;
    font-weight: 700;
    &:hover {
        cursor: pointer;
    }
}