// ! en cualquier .scss: con @use para usar variables, colocar carpeta raiz del archivo y ese archivo (_variables.scss). NO ES NECESARIO COLOCAR LA CARPETA RAIZ CUANDO EL ARCHIVO DONDE SE USARAN LAS VARIABLES SE ENCUENTRA EN LA MISMA CARPETA QUE LA DE LAS VARIABLES
@use "variables" as v; // lo pongo con sin su carpeta raiz pq esta en la misma carpeta

html {
    box-sizing: border-box;
    font-size: 62.5%;
    scroll-padding-top: 0rem;
}

*,
*::after,
*::before {
    box-sizing: inherit;
}

body {
    font-size: 1.6rem;
    font-family: v.$fuente_principal;
    &.fijar-body { // ! al parecer es obligatorio incluir este "&" cuando se trata de body (asi mi codigo si funciona)
        overflow: hidden;
    }
    &.body-scroll {
        padding-top: 8.6rem;
    }
}

p {
    font-size: 2rem;
    color: v.$negro;
    line-height: 2.5rem;
}

.contenedor {
    width: 90%;
    max-width: 120rem;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block; // ? no se realmente pq
    // max-width: 100%;
}

h1,
h2,
h3 {
    margin: 0 0 5rem 0;
}

h1 {
    font-size: 4rem;
}

h2 {
    font-size: 3.6rem;
}

h3 {
    font-size: 3rem;
}
