@use "base/variables" as v;
@use "base/mixins" as m;

.sobre-festival {
    padding: 5rem 0;
    @include m.tablet {
        @include m.grid(2, 3rem);
    }
}
.contenido-festival {
    @include m.tablet {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    h2 {
        margin-top: 3rem;
        margin-bottom: 2rem;
        @include m.tablet {
            margin-top: 0;
        }
    }
    .fecha {
        font-weight: 700;
        color: v.$rosa;
    }
}
